export default {
  components: {
    boxContent: require('~/components/common/box-content.vue').default,
    contactBtn: require('~/components/common/contact-btn.vue').default,
    titleText: require('~/components/common/title-text.vue').default
  },
  props: {
    projectDetail: {
      type: Array,
      default: () => []
    },
  },
  data () {
    return {
      swiperOption: {
        slidesPerView: 1,
        spaceBetween: 30,
        // loop: true,
        lazy: true,
        autoplay: {
          disableOnInteraction: false
        },
        pagination: {
            el: '.swiper-pagination',
            clickable: true
        }
      }
    }
  },
  mounted() {
    
    for (let i = 0; i < this.projectDetail.length; i++) {
      if(this.projectDetail[i].isRotate) {
        this.watchSwiper(i)
      }
    }
  },
  computed: {

  },
  methods: {
    watchSwiper(key) {
      // scroll down then play
      if (!!window.IntersectionObserver) {
        let observer = new IntersectionObserver(
          (entries, observer) => {
            entries.forEach((entry) => {
              this.$refs[`mySwiper${key}`][0].$swiper.autoplay.stop()
              if (entry.isIntersecting) {
                // console.log(entry)
                this.$refs[`mySwiper${key}`][0].$swiper.autoplay.start()
                

                // You can remove the observer if you do not need it
                observer.unobserve(entry.target)
              }
            });
          },
          { rootMargin: "0px 0px -200px 0px" }
        );

        // Add the observer to you swiper
        observer.observe(document.querySelector(`.case-box${key}`))
      } else {
        // You can use the polyfill or just start the autoplay
      }
    }
  },
  watch: {
    
  }
}
